import React from "react"
import { graphql, useStaticQuery, Link } from 'gatsby'
import styled from 'styled-components'
import { color, padding, margin, borderRadius, textSize, boxShadow } from '../../util/style_property'

const TagDrops = ({tags, isButton = false}) => {
  const drops = tags.map((tag) => {
    const tagDrop = isButton ? <TagDropButton tagData={tag} /> : <TagDrop tagData={tag} />
    return (tagDrop)
  })
  return drops

}

const TagDrop = ({ tagData}) => {
  const Drop = styled.p`
    display: block;
    padding: 0 ${padding.s};
    margin: ${margin.s};
    font-size: ${textSize.m};
    font-style: bold;
    border-radius: ${borderRadius.s};
  `
  return (
    <Drop >{tagData.name}</Drop>
  )
}

const TagDropButton = ({ tagData }) => {
  const topLeftMargin = parseInt(margin.s, 10) + 1 + "px"
  const bottomRightMargin = parseInt(margin.s, 10) - 1 + "px"
  const Drop = styled(Link)`
    padding: ${padding.s} ${padding.m};
    margin: ${margin.s};
    background-color: ${color.background_dark_gray};
    font-size: ${textSize.m};
    color: ${color.white};
    border-radius: ${borderRadius.s};
    box-shadow: ${boxShadow.l};
    transition: background-color 0.1s, color 0.1s;
    &:hover{
      margin: ${topLeftMargin} ${bottomRightMargin} ${bottomRightMargin} ${topLeftMargin};
      background-color: ${color.background_light_gray};
      color: ${color.text_black};
      text-decoration: none;
      box-shadow: ${boxShadow.s};
    }
  `
  return (
    <Drop to={"/tag/" + tagData.slug}>{tagData.name}</Drop>
  )
}

export default TagDrops